.experience__container {
  width: 1140px;
  margin: 5rem auto 0 auto;
}

.experience__container .heading__one {
  text-align: center;
  margin-bottom: 4rem;
}

.experience__container .resume__download__button {
  text-decoration: none;
  display: block;
  width: max-content;
  margin: auto;
}

.experience__container .resume__download__button button {
  background-color: var(--red);
  color: var(--white);
  padding: 0 1rem;
}

.experience__container .skill__list {
  display: flex;
  justify-content: space-around;
}

.experience__container .skill__list .skill__single__list .item {
  position: relative;
  margin-bottom: 3rem;
}

.experience__container .skill__list .skill__single__list .item .heading__three {
  font-size: 18px;
  font-family: var(--inter);
  font-weight: 500;
  margin-bottom: 5px;
}

.experience__container
  .skill__list
  .skill__single__list
  .item:not(:last-child)::before {
  width: 0;
  height: 150%;
  content: "";
  position: absolute;
  bottom: -100%;
  left: -50%;
  border-left: 2px dashed var(--gray);
}

.experience__container
  .skill__list
  .skill__single__list
  .item:last-child::before {
  top: -100%;
}

.skill__list .skill__single__list .item .circle {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  position: absolute;
  left: calc(-50% - 12px);
  top: 50%;
  transform: translateY(-50%);
}

.skill__list .skill__single__list .item .circle.color__one {
  background-color: var(--green);
}

.skill__list .skill__single__list .item .circle.color__two {
  background-color: var(--blue);
}

.skill__list .skill__single__list .item .circle.color__three {
  background-color: var(--yellow);
}

.skill__list .skill__single__list .item .circle.color__default {
  background-color: var(--green);
}

.skill__list .skill__single__list .item .circle .inner__circle {
  position: relative;
  width: 38px;
  height: 38px;
  border-radius: 50%;
  border: 2px dashed var(--gray);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media only screen and (max-width: 1280px) {
  .experience__container {
    width: 100%;
  }
}

@media only screen and (max-width: 1024px) {
  .experience__container .skill__list .skill__single__list .item {
    margin-bottom: 2rem;
  }

  .experience__container
    .skill__list
    .skill__single__list
    .item
    .heading__three {
    font-size: 16px;
  }

  .experience__container
    .skill__list
    .skill__single__list
    .item:not(:last-child)::before {
    border-width: 2px;
    left: -30%;
    bottom: -110%;
  }

  .skill__list .skill__single__list .item .circle {
    width: 16px;
    height: 16px;
    left: calc(-30% - 7px);
  }

  .skill__list .skill__single__list .item .circle .inner__circle {
    width: 25px;
    height: 25px;
  }

  .experience__container .heading__one {
    margin-bottom: 2rem;
  }
}

@media only screen and (max-width: 768px) {
  .experience__container {
    margin-top: 3rem;
  }

  .experience__container .skill__list {
    flex-direction: column;
    align-items: center;
  }

  .experience__container .skill__list .skill__single__list .item {
    margin-bottom: 1.5rem;
  }

  .experience__container
    .skill__list
    .skill__single__list
    .item
    .heading__three {
    margin-bottom: 0;
  }

  .experience__container .skill__list .skill__single__list .item {
    font-size: 14px;
  }
}

/* Styling for developer proficiencies */
.proficiencies {
  margin-top: 2rem;
}

.heading__two {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  text-align: center;
}

.proficiencies__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  list-style-type: none;
  padding: 0;
}

.proficiency__item {
  margin: 0.5rem;
  display: flex;
  align-items: center;
}

.proficiency__icon {
  background-color: #f0f0f0;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  margin-right: 0.5rem;
}

.proficiency__name {
  font-weight: 500;
}
